import { v2 } from "../maths/v2";
import { circle, image, rect } from "../systems/rendering/basicShapes";
import { getZoomScaledPosition, JSONifyComponent } from '../util/general';
import { rgb, rgba } from "../util/colour";
import { Renderer } from "./renderer";
export class Renderer2D extends Renderer {
    constructor(type, offset) {
        super(type);
        this.offset = v2.zero;
        this.addPublic({
            name: 'offset',
            value: offset,
            type: 'v2',
            description: 'offset from entity\'s transform\s position'
        });
    }
    Update() { }
    json() {
        return JSONifyComponent(this);
    }
}
export class CircleRenderer extends Renderer2D {
    constructor({ radius = 1, offset = new v2(0, 0), colour = rgb() }) {
        super("CircleRenderer", offset);
        this.radius = 1;
        this.colour = rgb();
        this.addPublic({
            name: 'radius',
            value: radius,
            type: 'number',
            array: false
        });
        this.addPublic({
            name: 'colour',
            value: colour,
            type: 'rgb',
            overrideSet: (value) => {
                if (typeof value === 'string') {
                    this.setPublic('colour', rgb.parse(value));
                    return;
                }
                this.setPublic('colour', value);
            }
        });
    }
    draw({ zoom, transform, ctx, position, center }) {
        const radius = this.radius * zoom * transform.scale.x;
        if (radius <= 0)
            return;
        circle(ctx, getZoomScaledPosition(position.clone.add(this.offset), zoom, center), radius, this.colour.rgb);
    }
}
export class RectRenderer extends Renderer2D {
    constructor({ height = 1, offset = new v2(0, 0), width = 1, colour = rgb(0, 0, 0), }) {
        super("RectRenderer", offset);
        this.height = 1;
        this.width = 1;
        this.colour = rgb();
        this.addPublic({
            name: 'height',
            value: height,
        });
        this.addPublic({
            name: 'width',
            value: width,
        });
        this.addPublic({
            name: 'colour',
            value: colour,
            type: 'rgb',
            overrideSet: (value) => {
                if (typeof value === 'string') {
                    this.setPublic('colour', rgb.parse(value));
                    return;
                }
                else if (typeof value.r === 'number') {
                    // raw json, so do last step of parsing it
                    this.setPublic('colour', rgba(value.r, value.g, value.b, value.a));
                    return;
                }
                this.setPublic('colour', value);
            }
        });
    }
    Start() { }
    draw(arg) {
        const width = this.width * arg.transform.scale.x * arg.zoom;
        const height = this.height * arg.transform.scale.y * arg.zoom;
        if (height <= 0 || width <= 0)
            return;
        let renderPos = this.offset.clone
            .add(arg.position);
        renderPos.sub(new v2(width / (arg.zoom * 2), height / (arg.zoom * 2)));
        rect(arg.ctx, getZoomScaledPosition(renderPos, arg.zoom, arg.center), width, height, this.colour.rgb, arg.transform.rotation.z);
    }
}
export class ImageRenderer2D extends Renderer2D {
    constructor({ height = 1, offset = new v2(0, 0), width = 1, url = '', }) {
        super("ImageRenderer2D", offset);
        this.height = 1;
        this.width = 1;
        this.url = '';
        this.addPublic({
            name: 'height',
            value: height,
        });
        this.addPublic({
            name: 'width',
            value: width,
        });
        this.addPublic({
            name: 'url',
            value: url,
            type: 'string',
            description: 'The path to the image to be rendered - always start with ../projects/{yourProjectID}/assets/',
        });
    }
    Start() { }
    draw(arg) {
        if (!this.url) {
            return;
        }
        const width = this.width * arg.transform.scale.x * arg.zoom;
        const height = this.height * arg.transform.scale.y * arg.zoom;
        if (height <= 0 || width <= 0)
            return;
        let renderPos = this.offset.clone
            .add(arg.position);
        renderPos.sub(new v2(width / (arg.zoom * 2), height / (arg.zoom * 2)));
        image(arg.ctx, getZoomScaledPosition(renderPos, arg.zoom, arg.center), new v2(width, height), this.url, arg.transform.rotation.z);
    }
}
